import state from './State';
import $ from './CustomJQuery';

function calculateNumberOfElementsOnPage() {
    const bodyElement = $('body');
    state.setElementsOnPage(1);
    if (bodyElement.clientWidth > 600) {
        state.setElementsOnPage(2);
    }
    if (bodyElement.clientWidth > 1024) {
        state.setElementsOnPage(3);
    }
    if (bodyElement.clientWidth > 1440) {
        state.setElementsOnPage(4);
    }
    const num = -state.leftElement / state.elementsOnPage * 100;
    $('.videos').style.left = `${num}vw`;
    // console.log($('.videos').style['left']);
}

export { calculateNumberOfElementsOnPage as default };



