import $ from './CustomJQuery';
import state from './State';
import drawElements from './DrawElements';
import updatePaging from './UpdatePaging';

function pager() {
    const videosContainer = $('.videos');
    let num = -state.leftElement / state.elementsOnPage * 100;
    const paging = document.getElementsByClassName('paging')[0];
    paging.addEventListener('click', (e) => {
        e.preventDefault();
        if (e.target.className.includes('page')) {
            // we want see transition on swipe
            videosContainer.style.transition = 'left 0.5s';
            num = (Number(e.target.innerHTML, 10) - 1) * (-100);
            videosContainer.style.left = `${num}vw`;
            state.setInResizing(false);
            state.setCurrentPage(Number(e.target.innerHTML, 10) - 1);
            state.setLeftElement(state.currentPage * state.elementsOnPage);
            if (state.lastRenderedElement - (state.leftElement + state.elementsOnPage + 4) < 0) {
                drawElements(state, state.leftElement + state.elementsOnPage -
                    state.lastRenderedElement - 1 + 4);
                // state.setLastRenderedElement(state.leftElement+state.elementsOnPage);
            }
            updatePaging();
        }
    });
    paging.addEventListener('mousedown', (e) => {
        e.preventDefault();
    });
}

export { pager as default };



