import $ from './CustomJQuery';
import state from './State';
import drawElements from './DrawElements';
import updatePaging from './UpdatePaging';

const request = {
    key: 'AIzaSyA5z46MEi2fhkPtf2M7EioI2AiHQiPrM88',
    type: 'video',
    part: 'snippet',
    maxResults: 15,
    order: 'relevance',
    q: '',
    pageToken: '',
};

let videos = {};

const requestState = {
    clearVideos: () => {
        videos = {};
    },
    setRequestQuery: (val) => {
        request.q = val;
    },
    setRequestPageToken: (val) => {
        request.pageToken = val;
    },
};

function updateRequest() {
    let apiUrl = 'https://www.googleapis.com/youtube/v3/search?';
    for (const property in request) {
        if (request.hasOwnProperty(property)) {
            apiUrl += `${property}=${request[property]}&`;
        }
    }
    return apiUrl;
}

function makeRequest() {
    const url = updateRequest();
    const req = new XMLHttpRequest();
    let tempElements;
    const reqStatistics = new XMLHttpRequest();
    let tempStatistics;
    req.onreadystatechange = () => {
        if (req.readyState === 4) {
            if (req.status === 200) {
                tempElements = JSON.parse(req.responseText);
                let idsStatistics = '';
                tempElements.items.forEach((currentValue) => {
                    idsStatistics += (currentValue.id.videoId + ',');
                });
                request.pageToken = tempElements.nextPageToken;
                const urlStatistics = `https://www.googleapis.com/youtube/v3/videos?key=${request.key}&id=${idsStatistics}&part=statistics,snippet`;
                reqStatistics.open('GET', urlStatistics, true);
                reqStatistics.send();
            } else {
                // console.log(req.statusText);
            }
        }
    };
    reqStatistics.onreadystatechange = () => {
        if (reqStatistics.readyState === 4) {
            if (reqStatistics.status === 200) {
                tempStatistics = JSON.parse(reqStatistics.responseText);
                if (!videos.items) {
                    Object.assign(videos, tempStatistics);
                    if (videos.items.length > 0) {
                        drawElements(state, state.elementsOnPage + 4);
                    } else {
                        $('.search').value = '';
                    }
                } else {
                    tempStatistics.items.forEach((currentValue) => {
                        videos.items.push(currentValue);
                    });
                }
                updatePaging();
            } else {
                // console.log(req.statusText);
            }
        }
    };
    req.open('GET', url, true);
    req.send();
}

export { requestState, makeRequest, videos };



