import $ from './CustomJQuery';
// import {state} from './State';
import { makeRequest, videos } from './Request';

function drawElements(state, number) {
    const videosContainer = $('.videos');
    let htmlToAppend = '';
    if (videos.items) {
        for (let i = state.lastRenderedElement + 1; i < state.lastRenderedElement
            + 1 + number; i += 1) {
            // console.log('adding i::',i);
            // console.log(videos);
            htmlToAppend +=
                '<div class="media-element">' +
                    `<a href="https://www.youtube.com/watch?v=${videos.items[i].id}" class="link">` +
                        `<img src="${videos.items[i].snippet.thumbnails.medium.url}">` +
                    '</a>' +
                    `<a href="https://www.youtube.com/watch?v=${videos.items[i].id}" class="link">` +
                        `<p class="title">${videos.items[i].snippet.title}</p>` +
                    '</a>' +
                    `<p class="uploader-name">${videos.items[i].snippet.channelTitle}</p>` +
                    `<p class="date-uploaded">${new Date(videos.items[i].snippet.publishedAt).toLocaleDateString()}</p>` +
                    `<p class="views">${videos.items[i].statistics.viewCount}</p>` +
                    `<p class="description">${videos.items[i].snippet.description}</p>` +
                '</div>';
        }
        videosContainer.innerHTML += htmlToAppend;
        // if(state.lastRenderedElement<state.leftElement+state.elementsOnPage-1){
        // state.setLastRenderedElement(state.leftElement+state.elementsOnPage-1);
        state.setLastRenderedElement(state.lastRenderedElement + 1 + number - 1);
        if (videos.items.length - state.leftElement <= 15) {
            makeRequest();
        }
        // console.log(state);
    }
}

export { drawElements as default };



