import $ from './CustomJQuery';
import { requestState, makeRequest } from './Request';
import state from './State';
import calculateNumberOfElementsOnPage from './CalculatePages';

function search() {
    const searchContainer = $('#search-input');
    searchContainer.addEventListener('keypress', (event) => {
        if (event.keyCode === 13) {
            $('.videos').innerHTML = '';
            $('.videos').style.left = '0vw';
            requestState.clearVideos();
            state.refresh();
            calculateNumberOfElementsOnPage(null);
            requestState.setRequestPageToken('');
            requestState.setRequestQuery(searchContainer.value);
            makeRequest();
            $('.search').blur();
        }
    });
}

export { search as default };




