import $ from './CustomJQuery';

function renderPage() {
    $('body').innerHTML =
        '<div class=wrapper><header><input type="text" class="search" id="search-input" type="text" placeholder="Search videos"/></header>' +
        '</div><main><div class="videos"></div></main><div class=wrapper><footer><ul class="paging"></ul></footer></div>';
}

export { renderPage as default };



