import $ from './CustomJQuery';
import state from './State';
import drawElements from './DrawElements';
import updatePaging from './UpdatePaging';
import calculateNumberOfElementsOnPage from './CalculatePages';
import { videos } from './Request';

function resizer() {
    const videosContainer = $('.videos');
    window.addEventListener('resize', (event) => {
        if (videos.items) {
            // we dont want ot see transition on resize
            if (!state.inResizing) {
                state.setPriorityElement(state.leftElement);
                state.setInResizing(true);
            }
            videosContainer.style.transition = 'left 0s';
            calculateNumberOfElementsOnPage(event);
            state.setCurrentPage(Math.floor(state.priorityElement / state.elementsOnPage));
            state.setLeftElement(state.currentPage * state.elementsOnPage);
            if (state.lastRenderedElement < state.leftElement + state.elementsOnPage - 1 + 4) {
                drawElements(state, state.leftElement + state.elementsOnPage - 1
                    + 4 - state.lastRenderedElement);
                state.setLastRenderedElement(state.leftElement + state.elementsOnPage - 1 + 4);
            }
            updatePaging();
            // console.log(state);
        }
    });
}

export { resizer as default };



