import state from './State';
import $ from './CustomJQuery';

function updatePaging() {
    const pagingContainer = $('.paging');
    let pages = Math.ceil((state.lastRenderedElement - 4) / state.elementsOnPage) + 1;
    let pagingElNumber = (state.lastRenderedElement - 4) / state.elementsOnPage + 1;
    if (state.elementsOnPage === 1) {
        pages += 1;
        pagingElNumber += 1;
    }
    $('.videos').style.width = `${(pages + 5) * 100}vw`;
    // console.log(pages);
    // console.log(state);
    if (pages <= 5) {
        let htmlToAppend = '';
        for (let i = 1; i < pagingElNumber + 1; i += 1) {
            if (i - 1 === state.currentPage) {
                htmlToAppend += `<li class="page current-page" id="page-${i}">${i}</li>`;
            } else {
                htmlToAppend += `<li class="page" id="page-${i}">${i}</li>`;
            }
        }
        pagingContainer.innerHTML = htmlToAppend;
    }
    if (pages > 5) {
        if (state.currentPage === 0) {
            pagingContainer.innerHTML =
                '<li class="page current-page" id="page-1">1</li>' +
                '<li class="page" id="page-2">2</li>' +
                '<li class="page" id="page-3">3</li>' +
                '<li class="page" id="page-4">4</li>' +
                `<li class="page" id="page-${pages}">${pages}</li>`;
        }
        if (state.currentPage === 1) {
            pagingContainer.innerHTML =
            '<li class="page" id="page-1">1</li>' +
            '<li class="page current-page" id="page-2">2</li>' +
            '<li class="page" id="page-3">3</li>' +
            '<li class="page" id="page-4">4</li>' +
            `<li class="page" id="page-${pages}">${pages}</li>`;
        }
        if (state.currentPage > 1 && state.currentPage < pages - 2) {
            pagingContainer.innerHTML =
            '<li class="page" id="page-1">1</li>' +
            `<li class="page" id="page-${state.currentPage}">${state.currentPage}</li>` +
            `<li class="page current-page" id="page-${state.currentPage + 1}">${state.currentPage + 1}</li>` +
            `<li class="page" id="page-${state.currentPage + 2}">${state.currentPage + 2}</li>` +
            `<li class="page" id="page-${pages}">${pages}</li>`;
        }
        if (state.currentPage === pages - 2) {
            pagingContainer.innerHTML =
            '<li class="page" id="page-1">1</li>' +
            `<li class="page" id="page-${pages - 3}">${pages - 3}</li>` +
            `<li class="page" id="page-${pages - 2}">${pages - 2}</li>` +
            `<li class="page current-page" id="page-${pages - 1}">${pages - 1}</li>` +
            `<li class="page" id="page-${pages}">${pages}</li>`;
        }
        if (state.currentPage === pages - 1) {
            pagingContainer.innerHTML =
            '<li class="page" id="page-1">1</li>' +
            `<li class="page" id="page-${pages - 3}">${pages - 3}</li>` +
            `<li class="page" id="page-${pages - 2}">${pages - 2}</li>` +
            `<li class="page" id="page-${pages - 1}">${pages - 1}</li>` +
            `<li class="page current-page" id="page-${pages - 0}">${pages - 0}</li>`;
        }
    }
}

export { updatePaging as default };



