const state = {
    currentPage: 0,
    elementsOnPage: 4,
    leftElement: 0,
    lastRenderedElement: -1,
    inResizing: false,
    priorityElement: 0,
    setCurrentPage: (val) => {
        state.currentPage = val;
    },
    setElementsOnPage: (val) => {
        state.elementsOnPage = val;
    },
    setLeftElement: (val) => {
        state.leftElement = val;
    },
    setLastRenderedElement: (val) => {
        state.lastRenderedElement = val;
    },
    setInResizing: (val) => {
        state.inResizing = val;
    },
    setPriorityElement: (val) => {
        state.priorityElement = val;
    },
    refresh: () => {
        state.currentPage = 0;
        state.elementsOnPage = 4;
        state.leftElement = 0;
        state.lastRenderedElement = -1;
        state.inResizing = false;
        state.priorityElement = 0;
    },
};

export { state as default };



